body{
    background-color: hsl(0, 0% , 10%);
}

.Main {
    border: 4px solid white; 
    padding-top: 20px;
    border-radius: 10px; 
    max-width: 800px; 
    margin: 0 auto; 
    background-color: rgba(255, 255, 255, 0.1); 
    overflow: hidden; 
}

.to-do-list{
    font-family:  Arial, sans-serif;
    text-align: center;
}

.header{
    padding: 20px;
}

h1 {
    font-size: 4rem;
    color: white;
    width: 500px; 
    border: 2px solid white; 
    padding: 30px; 
    margin: 0 auto;
    display: inline-block; 
    border-radius: 5px; 
}

.input_div {
    display: flex;
    justify-content: center; /* Center the input and button */
    margin-top: 20px;
}

button{
    font-size: 1.7rem;
    font-weight: bold;
    padding: 10px 20px;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.5s ease;
}

.add-button{
    background-color: hsl(125, 47%, 60%);
}

.add-button:hover{
    background-color: hsl(125, 47%, 20%);
}

.delete-button{
    background-color: hsl(0, 100%, 60%);
}

.delete-button:hover{
    background-color: hsl(0, 100%, 20%);
}

.move-button{
    background-color: hsl(247, 100%, 60%);
}

.move-button:hover{
    background-color:  hsl(247, 100%, 20%);
}

input[type="text"]{
    font-size: 1.6rem;
    padding: 10px;
    border: 2px solid hsla(0,0%,80%,0.5);
    color: hsla(0, 0%, 0%, 0.5);
    margin-right: 10px;
    max-width: 100%;
}

ol{
    padding: 0;
}

li{
    font-size: 2rem;
    font-weight: bold;
    padding: 15px;
    background-color: hsl(0, 0%, 97%);
    margin-bottom: 10px;
    border: 3px solid hsla(0, 0% , 85%, 0.75);
    border-radius: 5px;
    display: flex;
    align-items: center;
    flex: 1;
    min-width: 200px;
}

.text{
    flex:1 ;
}

.delete-button, .move-button{
    padding: 8px 12px;
    font-size: 1.4rem;
    margin-left: 10px;
}

.order-list{
    padding-left: 10px;
    padding-right: 10px;
}
